main {
    display: flex;
    align-items: center;
    height: 50vh;
}

.col-md-6 {
    padding-top: 5rem;
}
.separator {
    width: 100%;
    border-bottom: solid 1px;
    position: relative;
    margin: 30px 0px -30px;
}

.separator::before {
    content: "OR";
    position: absolute;
    left: 47%;
    top: -8px;
    background-color: #fff;
    padding: 0px 10px;
}